.footer {
    background-color: #191919;

    a#read-more {
        font-size: 1rem *(14/16);
        line-height: 20px;
        color: #d1d1d1;
        font-family: "Montserrat";
        font-weight: 400;

        &:hover {
            text-decoration: none;
        }
    }
    #triangle {
        margin-left: 5px;

        display: inline-block;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 4px 0 4px 5px;
        border-color: transparent transparent transparent #fff;

    }

    p#footer__yellow-p {
        color: #c7b299;
    }

    span#footer__white-span {
        color: #ffffff;
    }
    

    //main
    &__logo {
        width: 180px;

        margin-left: 0;
    }

    &__p-with-border {
        border-bottom: 1px solid #242424;
        padding-bottom: 25px;
    }

    &__images {
        display: flex;
        flex-flow: wrap;

        width: 300px;

        img {
            width: 130px;
            height: 100px;

            margin: 10px;
            margin-left: 0;

            padding: 5px;
        }
    }

    &__main {
        padding-top: (55/1024)*100vh;
        padding-bottom: (55/1024)*100vh;

        width: 95%;
        margin: 0 auto;

        display: flex;
        justify-content: space-around;
        flex-flow: wrap;


        &--elem {
            width: 240px;

            margin: 25px;

            h1 {
                font-size: 1rem*(24/16);
                color: #ffffff;
                font-family: "Raleway";
                font-weight: 700;
            }

            p {
                font-size: 1rem*(14/16);
                line-height: 24px;
                color: #555555;
                font-family: "Montserrat";
                font-weight: 300;

            }

            span {
                font-size: 1rem*(12/16);
                line-height: 20px;
                color: #363636;
                font-family: "Montserrat";
                font-weight: 300;
            }

            a {
                font-size: 1rem*(14/16);
                line-height: 20px;
                color: #00bff3;
                font-family: "Montserrat";
                font-weight: 300;

                &:hover {
                    text-decoration: underline;
                }
            }
        }

    }

    // bottom

    &__bottom {
        background-color: #101010;

        font-size: 1rem*(13/16);
        line-height: 20px;
        color: #555555;
        font-family: "Montserrat";
        font-weight: 300;

        display: flex;
        align-items: center;
        justify-content: space-around;

        &-border-div {
            height: 13px;
            border-left: 1px solid #252525;

            margin-left: 10px;
            margin-right: 10px;
            margin-top: 4px;
        }

        &-elem {
            display: flex;

            padding-top: (30/1024)*100vh;
            padding-bottom: (30/1024)*100vh;

            ul {
                display: flex;

            }
        }

        a {
            color: #af9c87;
            font-size: (20/16)*1rem;

            &:hover {
                text-decoration: underline;
            }
        }

        span {
            color: #959595;
        }
    }
}
// MEDIA----------------------------------------------------------------------

@media (max-width: 990px){
    .footer{
        &__bottom{
            flex-flow: column;

            &-elem:last-child{
                margin-top: (-42/1024)*100vh;
            }
        }
    }
}
@media (max-width: 610px){
    .footer{
        &__images{
            img{
                margin-left: -9px;
            }
        }
    }
}
@media (max-width: 410px){
    .footer__bottom{
        text-align: center;
        &-elem{
            padding-right: 10px;
            padding-left: 10px;
        }
    }
}