* {
    box-sizing: border-box;
}

 html, body{
    font-size: 16px;
}


.container {
    width: 100%;
    max-width: 1280px;
    margin: 0 auto;

    position: relative;
}

img{
    object-fit: none;
}

ul {
    list-style-type: none;
}

a {
    cursor: pointer;
    text-decoration: none;
    display: block;
}

input:focus,
button:focus {
    outline: none;
}



// slider---------------------------
.slider {
    display: -webkit-box;
    display: flex;
    justify-content: center;
    width: 88%;
    margin: 0 auto;
    
    &__arrow {
        margin-top: 190px;
        width: 350px;
        height: 90px;

        cursor: pointer;
    }
    &__slide {
        text-align: center;
        margin: 0 auto;
        
        &:focus {
            outline: none;
        }

    }
    .slick-dots {
        display: -webkit-box !important;
        display: flex !important; // slick-slider have style="display:block" for ul.slick-dots 
        -webkit-box-align: center;
        align-items: center;
        -webkit-box-pack: center;
        justify-content: center;
    }
    
    .slick-dots li {
        width: 10px;
        height: 10px;

        color: transparent;
    
        background-color: #8f8f91;
        border-radius: 100%;
        border: 2px solid transparent;
    }
    
    .slick-dots li.slick-active {
        border: 2px solid #d3c1ad;
        background-color: transparent;
    
        width: 12px;
        height: 12px;
    
    }
}

// transparent-block---------------
.transparent-block {
    border: none;
    background-color: transparent;
}

//2bg----------------------
.double-bg {
    background-size: cover;

    position: relative;
    z-index: 0;

    & * {
        z-index: 2;
    }

    &::before {
        content: '';
        width: 100%;
        height: 100%;
        background-color: black;
        opacity: 0.55;

        position: absolute;
        z-index: 1;
    }
}

// logo------------------
.logo {

    display: -webkit-box;

    display: flex;
    -webkit-box-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    align-items: center;

    margin-left: 35px;

    &__waxom {
        font-size: 34px;
        letter-spacing: -1px;
        color: #ffffff;
        font-family: "Raleway";
        font-weight: 400;
    }
}

// -----main-button------
.main-button {
    border-radius: 3px;
    border: none;
    background-color: #988574;
    width: 170px;
    height: 53px;
    cursor: pointer;


    font-size: (14/16)*1rem;
    line-height: 23px;
    color: #ffffff;
    font-family: "Raleway";
    font-weight: 800;

    transition: .2s; 
    
    &:hover{
        background-color: #bea895;
        transition: .2s; 
    }
}

// row-list----------------
.row-list {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-flow: wrap;

    li {
        text-align: center;
    }
}

// main-header----------------------
.main-header {
    &__h1 {
        font-size: (30/16)*1rem;
        color: #555555;
        font-family: "Raleway";
        font-weight: 600;
        text-align: center;

        padding-left: 5%;
        padding-right: 5%;

        &--white {
            color: #fff;
        }
    }

    &__p {
        width: 770px;
        height: 65px;
        font-size: 1rem;
        line-height: 28px;
        color: #8c8c8c;
        font-family: "Montserrat";
        font-weight: 300;
        text-align: center;

        margin: 0 auto;

        &--white {
            color: #fff;
        }
    }
}
// MEDIA---------------------------------------------------------------------
@media (max-width: 870px) {
    html, body{
       font-size: (100vw*0.02);
   }
   .main-header{
       &__p{
           width: 85%;
       }
   }
}

@media (max-width: 779px) {
    // ----
    .slider{
        &__arrow{
            width: 0;
        }
    }
}
@media (max-width: 660px){
    .main-button{
        width: (170/660)*100vw;
        height: (53/660)*100vw;
    }
    html, body{
        font-size: 14px;
    }
}
@media (max-width: 490px){
    .main-button{
        width: 140px;
        height: 46px;
    }
}
