.undermobile {
    background-image: linear-gradient(90deg, #211b19 0%, #4e3326 100%);

    margin-top: -16px;

    &__wrapper {

        padding: (80/1024)*100vh 5% (80/1024)*100vh 5%;
        flex-flow: wrap;

        li {
            width: 200px;

            position: relative;
            text-align: center;
            a{
                height: 100%;
                width: 100%;

                display: block;
                position: absolute;
            }
            h1 {
                font-size: (48/16)*1rem;
                line-height: 36px;
                color: #ffffff;
                font-family: "Montserrat";
                font-weight: 300;
            }

            p {
                font-size: (14/16)*1rem;
                line-height: 20px;
                color: #c7b299;
                font-family: "Montserrat";
                font-weight: 300;
            }

        }
    }

    &__underline {
        width: 30px;
        height: 2px;
        background-color: #ffffff;

        margin:  auto;
    }
}
// MEDIA------------------------------------------------------------------------------
@media (max-width: 999px){
    .undermobile{
        &__wrapper{
            padding-top: ((80/1024)*100vh)-(64/1024)*100vh;
            li{
                margin-top: (64/1024)*100vh;
            }
        }
    }
}
