.header {
    // background------------------------------------------
    background-image: url('../img/header/header-photo.png');
    padding: 0px 0px (40/1024)*100vh 0px;

    // -----------------------------------------------------------

    &__nav {
        display: flex;
        justify-content: space-around;
        align-items: center;

        padding-top: (35/1024)*100vh;
    }

    &__nav-hidden {
        color: white;
        font-size: 32px;

        display: none;
    }
    &__hidden-links{
        display: none;
    }


    &__links {
        display: -webkit-box;
        display: flex;
        -webkit-box-pack: justify;
        justify-content: space-between;

        width: 55%;
    }

    &__links-wrapper {
        display: flex;
        justify-content: space-between;

        width: 100%;
    }

    &__link {
        font-size: 14px;
        color: #ffffff;
        font-family: "Montserrat";
        font-weight: 300;

        border: 1px solid transparent;

        transition: .2s;

        &:hover {
            color: #c7b299;
            transition: .2s;
        }
    }



    // header-slider------
    &__slider {
        margin-top: (100vh* 208/1024);

        visibility: hidden;

        z-index: -1;
    }

    &__slide {
        font-family: "Raleway";
        color: #ffffff;

        padding-bottom: (100vh* 208/1024);

        & h2 {
            font-size: 1rem * (36/16);
            font-weight: 500;

        }

        & h1 {
            font-size: 1rem*(52/16);
            font-weight: 800;

        }

        & p {
            font-size: 1rem;
            line-height: 24px;
            font-family: "Montserrat";
            font-weight: 300;

            width: 40%;

            margin: 0 auto;
        }

    }

    // button-----

    &__main-button {
        margin-top: 50px;
    }
}

// MEDIA--------------------------------------------------------------------------

@media (max-width: 1200px) {
    .header {
        &__links {
            display: none;
        }

        &__nav-hidden {
            display: flex;
            flex-flow: column;
            align-items: center;
            text-align: center;

            width: 150px;
            height: 50px;

            margin-bottom: -20px;

            display: block;
        }

        &__hidden-links {
            display: block;
            position: fixed;

            z-index: 2000;

            top: 0;
            right: -150px;

            background-color: rgb(43, 43, 43);

            width: 150px;
            height: 100vh;

            transition: .4s;

            #fa-times {
                display: none;

                margin-top: (10/1024)*100vh;
                margin-left: 15px;

                color: white;
                font-size:26px;

                &:hover {
                    color: #c7b299;
                    cursor: pointer;
                    transition: .2s;
                }
            }
        }

        &__links-wrapper {
            flex-flow: column;
            padding: 0;
            margin: 0;
            display: flex;

            height: 68vh;

            margin-top: (30/1024)*100vh;

            li {
                text-align: center;
            }
        }

        .fa-bars:hover {
            color: #c7b299;
            cursor: pointer;
            transition: .2s;
        }
    }
}

@media (max-width: 992px) {
    .header {
        &__slide {
            p {
                width: 80%;
            }
        }
    }
}

