.underheader {
    &__list {
        padding: (100vh*20/1024) 0px (100vh*80/1024) 0px;
        width: 90%;

        margin: 0 auto;

        li {
            width: 268px;

            margin-top: 60px;
            position: relative;


            a {
                width: 100%;
                height: 100%;

                position: absolute;
                
                display: block;
            }

            h1 {
                font-size: (22/16)*1rem;
                color: #555555;
                font-family: "Raleway";
                font-weight: 600;
            }

            p {
                font-size: (14/16)*1rem;
                line-height: 24px;
                color: #8c8c8c;
                font-family: "Montserrat";
                font-weight: 300;
            }
        }
    }

    &__underline {
        width: 70px;
        height: 3px;
        background-color: #f1eee9;

        margin: 0 auto;
    }
}
