.projects {
    padding: (100vh*80/1024) 0px (100vh*80/1024) 0px;

    &__button{
        margin-top: (20/1024)*100vh;
    }

    &__slide-wrapper {
        display: flex;
        justify-content: space-around;
        flex-flow: wrap;

        &-elem {
            margin: (20/1024)*100vh 0px (100vh*20/1024) 2%;
            width: 300px;

            position: relative;

            a {
                width: 100%;
                height: 100%;

                display: block;
                position: absolute;
            }

            img {
                width: 100%;
                height: 237px;
            }

        }
    }

    &__image-text {
        text-align: left;

        padding: (100vh*25/1024) 0px (100vh*25/1024) 6%;
        background-color: #fbfaf8;

        margin-top: (-7/768)*100vh;
        
        h1 {
            font-size: (20/16)*1rem;
            color: #c7b299;
            font-family: "Raleway";
            font-weight: 500;

            margin-bottom: 3px;
            margin-top: 0;
        }

        span {
            font-size: (14/16)*1rem;
            line-height: 20px;
            color: #d1d1d1;
            font-family: "Montserrat";
            font-weight: 300;
        }
    }

    // slider------------
    &__slider {
        margin-top: (100vh*110/1024);
    }

