// MEDIA-------------------------------------------------------------------
@media(max-width: 1158px){
    .projects{
        .slick-dots {
            bottom: 1150px;
        }
    }
}
@media(max-width: 709px){
    .projects{
        .slick-dots {
            bottom: 2180px;
        }
    }
}
@media (max-width: 660px){
    .projects{
        .slick-dots {
           bottom: 2150px;
        }
    }
}
@media (max-width: 480px){
    .projects{
        .slick-dots li{
            width: (120/660)*100vw;
            height: (32/660)*100vw;
        }
        .slick-dots li.slick-active{
            width: (120/660)*100vw;
            height: (32/660)*100vw;
        }
        .slick-dots {
            bottom: 2120px;
        }
    }
}
@media (max-width: 370px){
    .projects{
        .slick-dots li{
            width: 52px;
            height: 18px;

            font-size: 6.5px;
        }
        .slick-dots li.slick-active{
            width: 52px;
            height: 18px;

            font-size: 6.5px;
        }
    }
}