.for-mobile {
    // padding-bottom: (300/1024)*100vh;

    padding-top: (180/1024)*100vh;

    &__wrapper{
        display: flex;
        justify-content: space-around;
    }

    &__img{
        max-width: 100%;
    }
    &__img-1{
        display: none;
    }

    &__text {
        width: (400/1280)*100vw;

        // margin-left: (900px/1920px)*100%;

        margin-top: (100/1024)*100vh;

        h1 {
            font-size: (30/16)*1rem;
            color: #555555;
            font-family: "Raleway";
            font-weight: 600;
        }

        p {
            font-size: 1rem;
            line-height: 28px;
            color: #8c8c8c;
            font-family: "Montserrat";
            font-weight: 300;
        }

        ul {
            font-size: 1rem;
            line-height: 32px;
            color: #8c8c8c;
            font-family: "Montserrat";
            font-weight: 300;

            li{
                display: flex;
                align-items: center;

                img{
                    margin-right: 10px;
                }
            }
        }
    }
}
// MEDIA---------------------------------------------------------------------
@media(max-width: 990px){
    .for-mobile{
        &__wrapper{
            flex-flow: column;
            text-align: center;
        }
        &__img{
            display: none;
        }
        &__img-1{
            margin: (120/1024)*100vh auto 0 auto;
            display: block;

            object-fit: cover;
        }

        &__text{
           margin: 0 auto;

           width: 80%;

            ul{
                margin: 0 auto;
                padding: 0;

                display: flex;
                flex-flow: column;
                align-items: center;
                
            }
        }
    }
}

@media(max-width: 480px){
   .for-mobile{
       display: none;
   }
}