.ideas {

    background-color: #fbfaf8;
    padding-top: 100vh*(80/1024);

    &__header{
        margin-bottom: 10vh;
    }
    &__main {
        height: 482px;

        margin-top: 100vh*(40/1024);

        position: relative;

        img {
            position: absolute;
        }

        .ideas__center-img {
            z-index: 1;

            margin: 0 auto;

            display: block;
            position: relative;
        }

        .ideas__left-img {
            left: 2.5%;
            top: 82px;
        }

        .ideas__right-img {
            right: 2.5%;
            bottom: 0px;
        }
    }

    &__bottom {
        padding: 100vh*(100/1024) 0px 100vh*(100/1024) 0px;

        background-image: linear-gradient(90deg, #211b19 0%, #4e3326 100%);

        &-wrapper {
            display: flex;
            justify-content: space-around;
            align-items: center;

            width: 100%;
            max-width: 1280px;
            margin: 0 auto;
        }
    }

    &__bottom-text {

        width: 50%;

        h1 {
            font-size: (28/16)*1rem;
            color: #bab6b4;
            font-family: "Raleway";
            font-weight: 400;
        }

        span {
            color: #c7b299;
        }

        div {
            font-size: (18/16)*1rem;
            color: #615855;
            font-family: "Montserrat";
            font-weight: 300;

            margin-top: 100vh*(-12/1024);
        }
    }
}

// MEDIA-----------------------------------------------------
@media(max-width: 900px) {
    .ideas {
        &__main {
            height: auto;

            img {
                position: static;
                display: block;
                margin: 0 auto;
                padding-bottom: (40/1024)*100vh;

                max-width: 90vw;

                object-fit: cover;
            }
        }
    }
}
@media(max-width: 660px) {
    .ideas {
        &__bottom-wrapper {
            flex-flow: column;
            justify-content: space-around;
            text-align: center;
        }
        &__bottom-text {
            padding-bottom: 20px;

            width: 90%;
        }
        button{
            margin-top: (60/1024)*100vh;
        }
    }
}