.video {
    background-image: url('../../build/img/video/video-bg.png');
    background-size: cover;

    .main-video {
        width: 100%;
        height: 100%;

        display: none;
    }

    &__wrapper {
        padding-top: 100vh*(220/1024);
        padding-bottom: 100vh*(220/1024);
    }

    &__pre {
        display: block;
    }

    #playpause {
        display: flex;
        justify-content: center;
        align-items: center;

        color: #fff;
        font-size: (32/16)*1rem;

        margin: 0 auto;

        width: 100px;
        height: 100px;
        border-radius: 50%;
        background-color: rgba($color: #000000, $alpha: 0.2);
        cursor: pointer;
    }

    span {
        font-size: (14/16)*1rem;
        line-height: 24px;
        color: #c7b299;
        font-family: "Montserrat";
        font-weight: 400;
        text-align: center;

        display: block;
    }
}

// MEDIA----------------------------------------------------------------
@media (max-width: 500px) {
    .video {
        #playpause {
            width: (100/500)*100vw;
            height: (100/500)*100vw;
        }
    }
}